@white: #fff;
@black: #000;
@normal-grey: #d3d3d3;
@light-grey: #e4e4e4;
@body-green: #4aa272;
@body-red: #d32f2f;
@turquoise: #1abc9c;
@greensea: #16a085;
@emerland: #2ecc71;
@nephritis: #27ae60;
@peterriver: #3498db;
@belizehole: #2980b9;
@amethyst: #9b59b6;
@wisteria: #8e44ad;
@sunflower: #f1c40f;
@orange: #f39c12;
@carrot: #e67e22;
@pumpkin: #d35400;
@alizarin: #e74c3c;
@pomegranade: #c0392b;
@clouds: #ecf0f1;
@silver: #bdc3c7;
@concrete: #95a5a6;
@asbestos: #7f8c8d;
@wetasphalt: #34495e;
@midnightblue: #2c3e50;

@font-face {
  font-family: 'Roboto Regular';
  src: url('/fonts/Roboto-Regular.ttf');
}
@font-face {
  font-family: 'FontAwesome';
  src: url('/fonts/fontawesome-webfont.ttf');
}

body{
  font-family: 'Roboto Regular' !important;
  margin:0;
}

.clear{
  clear: both;
}
.display-none{
  display: none;
}

.border-radius(@radius){
  border-radius: @radius;
  -moz-border-radius: @radius;
  -webkit-border-radius: @radius;
}
.transition(){

}

button{
  font-family: 'Roboto Regular';
  height: 40px;
  width: 150px;
  color: @clouds;
  background: @nephritis;
  border: 0;
  font-size: 16px;
  &:hover{
    background: @black;
  }
}
a{
  color: @black;
  text-decoration: none;
  cursor: pointer;
  border-bottom: 1px dotted @greensea;
  &:hover{
    color: @greensea;
  }
}
input{
  background: @white;
  border: 1px solid @normal-grey;
  padding: 6px;
  font-size: 15px;
  font-family: 'Roboto Regular';
}

select{
  width: 500px;
  height: 30px;
  background: @white;
  margin: 5px 0;
}
select[multiple='true']{
  height: auto;
  option{
    font-size: 20px;
    margin: 0 0 2px;
    &:checked{
      background: @greensea linear-gradient(0deg, @greensea 0%, @greensea 100%);
    }
  }
}

.pager-wrap{
  width: 100%;
  .pager{
    text-align: center;
    margin: 20px 0;
    button{
      background: @peterriver;
      width:40px;
      &:hover{
        background: @black;
      }
    }
    .current{
      background: @black;
    }
    span{
      padding: 10px;
      font-weight: bold;
    }
  }
}

button.danger{
  background: @alizarin;
  &:hover{
    background: @pomegranade;
  }
}
button.move{
  background: @peterriver;
  &:hover{
    background: @belizehole;
  }
}

.fa {
  display: inline-block;
  font: normal normal normal 20px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 19px;
}

span.radio{
  color: @asbestos;
  font-size: 13px;
  line-height: 2;
  cursor: pointer;
  &-positive{
    color: @greensea;
    font-size: 18px;
  }
  &-negative{
    color: @alizarin;
    font-size: 18px;
  }
}


.container{

}

input[type=radio]{
  vertical-align: top;
  width: 17px;
  height: 17px;
  margin: 0 3px 0 0;
  & + label {
    cursor: pointer;
    font-size: 17px;
    line-height: 30px;
  }
  &:not(checked) {
    position: absolute;
    opacity: 0;
    & + label {
      position: relative;
      padding: 0 0 0 35px;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 22px;
        height: 22px;
        border: 1px solid #CDD1DA;
        border-radius: 50%;
        background: #FFF;
      }
      &:after {
        content: '';
        position: absolute;
        top: 4px;
        left: 4px;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background: @nephritis;
        opacity: 0;
        transition: all .2s;
      }
    }
  }
  &:checked + label:after {
    opacity: 1;
  }
}
input[type=checkbox]{
  vertical-align: top;
  width: 17px;
  height: 17px;
  margin: 0 3px 0 0;
  & + label {
    cursor: pointer;
    font-size: 17px;
    line-height: 30px;
  }
  &:not(checked) {
    position: absolute;
    opacity: 0;
    & + label {
      position: relative;
      padding: 0 0 0 35px;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 22px;
        height: 22px;
        border: 1px solid #CDD1DA;
        border-radius: 20%;
        background: #FFF;
      }
      &:after {
        content: '';
        position: absolute;
        top: 4px;
        left: 4px;
        width: 16px;
        height: 16px;
        border-radius: 20%;
        background: @nephritis;
        opacity: 0;
        transition: all .2s;
      }
    }
  }
  &:checked + label:after {
    opacity: 1;
  }
}

